.header {
  display: flex;
  justify-content: space-between;
  /* background-color: #ADD8E6; */
  background-color: #DEDDED;
  align-items: center;
  padding: 10px;
  height:3rem;
}

.logo {
  
  left: 10rem;
  width: 6rem;
  height: 4rem;
  opacity: 1;
}

.left-header {
  flex: 1;
}

.right-header {
  display: flex;
}
.search-container {
  position: relative;
}

.search-input {
  width: 8rem;
  padding: 1rem 5rem 0.5rem 10px;
  border: 1px solid #6e4c85; 
  border-radius: 20px;
  outline: none;
  opacity: 0; 
  transition: opacity 0.3s ease, width 0.3s ease;
  top: 50%;
  left: 20px;
}

.search-icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  margin-top: 10px;
  transform: translateX(-140%);
  height: auto;
  cursor: pointer;
}
.search-container:hover .search-input {
  width: 200px; 
  opacity: 1; 
}
.search-icon:hover {
  opacity: 0.8;
}

.search-input:focus {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 10px;
}
